
.start-btn{
  display: block;
  margin: auto;
  padding: 7px;
  color: white;
  width: 100px;
  font-size: large;
  border-radius: 20px;
  background-color: #ffa8B6;
  cursor: pointer;
  box-shadow: 2px 3px black;
  transition: all;
  transition-duration: 1s;
}
.start-btn:hover{
  background-color: rgb(0, 0, 0);
  padding: 10px;
  width: 120px;
  font-size: 25px;

}
.counter{
  display: none;
 
}
.counter{
  font-size: 48rem;
  color: aliceblue;
}
.start-btn-container{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: rgb(76, 223, 233);
}
.top-section{
  display: none;
  background-image: url("top-bg.png");
  background-size: cover;
  width: 100vw;
  height: 100vh;
  border-bottom-left-radius: 90vh;
}
.top-logo{
  height: 55px;
  margin: 10px;
  width: 55px;
  background-color: rgb(187, 189, 190);
  border-radius: 55px;
}
.top-section-body{
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.top-hi-container{
  width: 350px;
  height: 350px;
  position: relative;
  margin: auto;
  border-radius: 200px;
  overflow: hidden;
  background-color: aliceblue;
}
.hi-top{
  width: 350px;
  height: 350px;
  border-radius: 200px;

}
.hand-top{
  width: 124px;
    height: 180px;
    position: absolute;
    bottom: 7px;
    left: 26px;
    transform: rotate(1deg);
    border-bottom-left-radius: 250px;
    animation-name: shakingHand;
    animation-duration: 2s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
}

.top-text-area{
  position: absolute;
  top: 45px;
  left: 100px;
  display:none;
 
}
.top-text-area2{
  position: absolute;
  top: 45px;
  left: 39px;
  display:none;
}
.top-text-area3{
  position: absolute;
  top: 45px;
  text-align: center;
  left: 45px;
  display:none;
}
.top-text-area4{
  position: absolute;
  top: 45px;
  text-align: center;
  left: 55px;
  display:none;
}

@keyframes shakingHand{
  0%{
    transform: rotate(1deg);
    bottom: 7px;
    left: 26px;
  }
  25%{
    transform: rotate(20deg);
    bottom: -4px;
    left: 52px;
  }
  50%{
    transform: rotate(1deg);
    bottom: 7px;
    left: 26px;
  }
  75%{

    transform: rotate(-20deg);
    bottom: -12px;
    
    left: 2px;
  }
  100%{
    transform: rotate(1deg);
    bottom: 7px;
    left: 26px;
  }
  /* transform: rotateX(20deg); */
}

/* About section second section Start*/
#about-section{
  height: 100vh;
  background-color: rgb(255, 255, 255);
  display: none;
}

/* About section second section End*/
/* Media Query Section */
@media only screen and (max-width: 450px) {
  .top-hi-container{
    width: 230px;
    height: 230px;
  }
  .hi-top{
    width: 230px;
    height: 230px;
    border-radius: 150px;
  
  }
  .hand-top{
    width: 82px;
    height: 125px;
    bottom: -5px;
    left: 15px;
      transform: rotate(1deg);
  }

  @keyframes shakingHand{
    0% {
      transform: rotate(1deg);
      bottom: -5px;
      left: 15px;
  }
  
  25% {
      transform: rotate(20deg);
      /* bottom: -15px; */
      left: 30px;
  }
  50% {
      transform: rotate(1deg);
      bottom: -5px;
      left: 15px;
  }
  75% {
      transform: rotate(-20deg);
      bottom: -9px;
      left: -3px;
  }
  100% {
      transform: rotate(1deg);
      bottom: -5px;
      left: 15px;
  }
    /* transform: rotateX(20deg); */
  }

  .top-text-area{
    top: 21px;
    left: 65px;
  }
  .top-text-area2{
    top: 29px;
    font-size: 10px;
    left: 28px;
  }
  .top-text-area3{
    top: 29px;
    text-align: center;
    left: 38px;
    font-size: x-small;
  }
  .top-text-area4{
    top: 22px;
    text-align: center;
    left: 34px;
    font-size: small;
   
  }
  
}

/* Media Query Section End*/